import React from "react";
import styled from "styled-components";
import SecondaryButton from "../buttons/SecondaryButton";
import { H2, BodyIntro, MediumText } from "../styles/TextStyles";

export default function ServiceSection() {
    return (
        <Wrapper>
            <ContentWrapper>
                <TextContent>
                    <H2>Lorem Ipsum</H2>
                    <MediumText>
                        directory in your code editor of choice and edit
                        src/pages/index.js. Save your changes and the browser
                        will update in real time!
                    </MediumText>
                </TextContent>
                <CardsWrapper>
                    <Card>
                        <img alt="x" src="/images/logos/illustration1.svg" />
                        <BodyIntro>Web design</BodyIntro>
                        <Description>
                            We are experts on electronic design,prototyping and
                            production
                        </Description>
                    </Card>
                    <Card className="card1">
                        <img alt="x" src="/images/logos/illustration2.svg" />
                        <BodyIntro>Mobile development</BodyIntro>
                        <Description>
                            Sapien eget mi proin sed. Neque laoreet suspendisse
                            interdum consectetur libero id.
                        </Description>
                    </Card>
                    <Card className="card2">
                        <img alt="x" src="/images/logos/illustration1.svg" />
                        <BodyIntro>Vehicule tracking</BodyIntro>
                        <Description>
                            We are experts on electronic design,prototyping and
                            production
                        </Description>
                    </Card>
                    <Card className="card3">
                        <img alt="x" src="/images/logos/illustration2.svg" />
                        <BodyIntro>Software</BodyIntro>
                        <Description>
                            Vulputate eu scelerisque felis imperdiet proin
                            fermentum leo vel orci.
                        </Description>
                    </Card>
                </CardsWrapper>
                <ButtonWrapper>
                    <SecondaryButton title="More services" />
                </ButtonWrapper>
            </ContentWrapper>
        </Wrapper>
    );
}

const Wrapper = styled.div`
    color: white;
`;

const ContentWrapper = styled.div`
    max-width: 1234px;
    margin: auto;
    padding: 120px 0px;
`;

const TextContent = styled.div`
    text-align: center;
    display: grid;
    gap: 20px;
    max-width: 400px;
    margin: auto;
    padding: 0px 30px;
`;

const CardsWrapper = styled.div`
    padding: 80px 30px;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(4, minmax(240px, 1fr));
    justify-content: space-between;
    overflow-x: scroll;
    -ms-overflow-style: none;
    ::-webkit-scrollbar {
        display: none;
    }
`;

const Card = styled.div`
    background: linear-gradient(180deg, #ff7373 0%, #491eb8 100%);
    border-radius: 20px;
    width: 240px;
    min-height: 330px;
    padding: 20px;
    text-align: center;
    cursor: pointer;
    *,
    & {
        transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    > :nth-child(2) {
        margin-top: 20px;
    }
    :hover {
        transform: scale(1.08);
        img {
            transform: scale(0.9) translateY(-15px);
        }
    }

    &.card1 {
        background: linear-gradient(180deg, #4bbeff 0%, #216bda 100%);
    }

    &.card2 {
        background: linear-gradient(180deg, #ffd78a 0%, #f4762d 100%);
    }
    &.card3 {
        background: linear-gradient(180deg, #7ef29d 0%, #0f68a9 100%);
    }
`;

const Description = styled.p`
    padding: 8px 0;
`;

const ButtonWrapper = styled.div`
    display: grid;
    justify-content: center;
`;
